import React from 'react';
import Webcam from "react-webcam";
import Quagga from 'quagga'; // ES6

import './BarcodeReader.scss';

const videoConstraints = {
    width: 1280,
    height: 720,
    // facingMode: "user"
};

const WebcamCaptureDevices = () => {
    const [deviceId, setDeviceId] = React.useState({});
    const [devices, setDevices] = React.useState([]);
   
    const handleDevices = React.useCallback(
      mediaDevices =>
        setDevices(mediaDevices.filter(({ kind }) => kind === "videoinput")),
      [setDevices]
    );
   
    React.useEffect(
      () => {
        navigator.mediaDevices.enumerateDevices().then(handleDevices);
      },
      [handleDevices]
    );
   
    return (
      <>
        {devices.map((device, key) => (
            <div>
              <Webcam 
                audio={false}
                height={300}
                width={300}
                videoConstraints={{ 
                    ...videoConstraints,
                    deviceId: device.deviceId
                }} />
              {device.label || `Device ${key + 1}`}
            </div>
   
          ))}
      </>
    );
};

const WebcamCapture = () => {
    const [result, setResult] = React.useState("");
    const webcamRef = React.useRef(null);

    const capture = React.useCallback(
        () => {
            setInterval(() => {
                const imageSrc = webcamRef.current.getScreenshot();
                // console.log("capture", imageSrc);
                Quagga.decodeSingle({
                    decoder: {
                        readers: ["code_128_reader"] // List of active readers
                    },
                    locate: true, // try to locate the barcode in the image
                    src: imageSrc,
                    // src: '/test/fixtures/code_128/image-001.jpg' // or 'data:image/jpg;base64,' + data
                }, function (result) {
                    if (result && result.codeResult) {
                        setResult(result.codeResult.code)
                        console.log("result", result.codeResult.code);
                    } else {
                        console.log("not detected");
                    }
                });
            }, 1000)
        },
        [webcamRef]
    );

    React.useEffect(
        () => {
            navigator.mediaDevices.enumerateDevices().then(capture);
        },
        [capture]
    );

    return (
        <div>
            <h1>{result || "_____________"}</h1>
            <Webcam
                audio={false}
                height={300}
                width={300}
                ref={webcamRef}
                screenshotFormat="image/jpeg"
                videoConstraints={videoConstraints}
            />
        </div>
    );
};

export default class BarcodeReader extends React.Component {
    constructor(props) {
        super(props)
        this.id = "BarcodeReader__asdasd"

        this.state = {
            result: '',
        }
    }

    componentDidMount() {

    }

    render() {

        return (
            <>
                <h2 style={{
                    gridColumnStart: 1,
                    gridColumnEnd: 3,
                    color: 'blue'
                }} >{this.state.result || '______________'}</h2>
                <div className="BarcodeReader" style={{
                    gridColumnStart: 1,
                    gridColumnEnd: 3,
                }} >
                    {/* <div id={this.id}
                        style={{
                            width: '100%',
                            height: "100px"
                        }}
                    ></div> */}
                    <WebcamCaptureDevices />
                </div>
            </>
        )
    }
}